import extend from "../utils/extend";

export default function(page, name) {
	if (!page.name && name) {
		page.name = name;
	}
	return extend(
		true,
		{
			methods: {
				// 
				init() {},
				// 
				triggerResizeChange() {
					this.$nextTick(() => {
						this.$store.dispatch("trigger", {
							eventName: this.resizeChangeEventName,
							args: []
						});
					});
				},
				// 
				filterChange(filter, filterProperty) {
					if (filterProperty) {
						this.$set(this[filterProperty], filter.name, filter.value);
					} else {
						let propertyObject = site.utils.setObjectProperty(this, filter.name, filter.value);
						if (propertyObject.propertyName != null) {
							this.$set(this[propertyObject.rootName], propertyObject.propertyName, propertyObject.rootValue[propertyObject.propertyName]);
						} else {
							this[propertyObject.rootName] = propertyObject.rootValue;
						}
					}
				},
				// 
				search() {
					this.filters.queryId = site.utils.generateGuid();
				},
				/**
				 * 
				 * @param isReset ()
				 */
				refresh(isReset = true) {
					// 
					this.filters.queryId = site.utils.generateGuid();
				},
				// 
				confirmHandle(yesHandler, actionName) {
					// TODO: 
					this.$confirm("" + (actionName || "") + "?", "", {
						confirmButtonText: "",
						cancelButtonText: "",
						type: "warning"
					}).then(() => {
						return yesHandler();
					});
				},
				getNextRow(row, tableRef = "data-table") {
					if (!row) return;
					if (!this.$refs[tableRef]) {
						// throw Error("ref");
						site.log.error("ref!");
						return;
					}
					return this.$refs[tableRef].getNearestRow(row, 1);
				},
				getPrevRow(row, tableRef = "data-table") {
					if (!row) return;
					if (!this.$refs[tableRef]) {
						// throw Error("ref");
						site.log.error("ref!");
						return;
					}
					return this.$refs[tableRef].getNearestRow(row, -1);
				},
				// 
				preBatchDelete(deleteHandler) {
					if (this.selectRows.length == 0) {
						this.$toastr.warning("");
						return false;
					}
					this.$confirm("?", "", {
						confirmButtonText: "",
						cancelButtonText: "",
						type: "warning"
					}).then(() => {
						Promise.all(
							this.selectRows.map(item => {
								return deleteHandler(
									{
										id: item.id
									},
									{
										isShowError: false,
										isResultData: false
									}
								)
									.then(() => {
										return true;
									})
									.catch(error => {
										// ,
										return error;
									});
							})
						).then(results => {
							if (results && results.length) {
								results.forEach((item, index) => {
									if (item == true) {
										this.$toastr.success((this.selectRows[index].name || "") + "");
									} else {
										this.$toastr.error((this.selectRows[index].name || "") + ((item.error && item.error.message) || ""));
									}
								});
							} else {
								this.$toastr.success("");
							}
							this.search(true);
						});
					});
				},

				/**
				 * 
				 * @param {Function} handerAction Promise
				 * @param {String} handlerName ,""
				 * @param {Function} handerSuccessAction ,"this.queryList"
				 * @param {handerRows} handerRows ,,"this.selectRows"
				 *
				 */
				// TODO: review
				handlerBatch(handerAction, handlerName, handerSuccessAction, handerRows) {
					if (typeof handerRows === "undefined") {
						handerRows = this.selectRows;
					}
					if (!handerSuccessAction) {
						handerSuccessAction = this.search;
					}
					handlerName = handlerName || "";
					if (!handerRows || handerRows.length === 0) {
						this.$toastr.warning("" + handlerName + "");
						return;
					}
					this.$confirm("" + handlerName + "?", "", {
						confirmButtonText: "",
						cancelButtonText: "",
						type: "warning"
					})
						.then(() => {
							Promise.all(
								handerRows.map(function(item) {
									return handerAction(item)
										.then(result => {
											if (result && result.data) {
												return result.data;
											} else if (result) {
												return result;
											} else {
												return { success: true };
											}
										})
										.catch(error => {
											// ,
											return error.response.data;
										});
								})
							).then(results => {
								if (results && results.length) {
									results.forEach((item, index) => {
										if (item.success != false) {
											this.$toastr.success((handerRows[index].name || "") + handlerName + "", null, { timeOut: 3000 });
										} else {
											this.$toastr.error((handerRows[index].name || "") + item.error.message, null, { timeOut: 3000 });
										}
									});
								} else {
									this.$toastr.success(handlerName + "");
								}
								handerSuccessAction();
							});
						})
						.catch(() => {});
				}
			}
		},
		page,
		{
			data() {
				let data = {};
				if (page.data && typeof page.data == "function") {
					data = page.data.call(this);
				}
				return extend(
					true,
					{
						filters: {
							queryId: null // IDIDsearch-formsearch
						},
						initParameters: {}, // 
						selectRows: [], // 
						resizeChangeEventName: this.$route.name + "_resize-change" // 
					},
					data
				);
			},
			mounted() {
				if (page.mounted && typeof page.mounted == "function") {
					page.mounted.call(this);
				}
				this.init();
				this.initParameters = site.utils.extend(true, {}, site.constants.QUERY_TABLE_DEFAULT_PARAMETERS, this.filters);
			}
		}
	);
}
