<template>
	<div class="site">
		<router-view class="page"></router-view>
	</div>
</template>

<script>
export default {
	data: function() {
		return {
			browserType: null // Opera|Firefox|Chrome|Safari|IE|Edge
		};
	},
	created() {
		this.init();
	},
	mounted() {
		//TODO: 
		this.initBrowser();
	},
	methods: {
		init: function() {},
		initBrowser() {
			let userAgent = navigator.userAgent,
				isShowBrowserWarning = true; //userAgent
			if (userAgent.indexOf("Opera") != -1 || userAgent.indexOf("OPR") != -1) {
				// Opera
				this.browserType = "Opera";
			} else if (userAgent.indexOf("compatible") != -1 && userAgent.indexOf("MSIE") != -1) {
				// IE
				this.browserType = "IE";
			} else if (!!window.ActiveXObject || "ActiveXObject" in window) {
				return "IE>=11";
			} else if (userAgent.indexOf("Edge") != -1) {
				//Edge
				this.browserType = "Edge";
			} else if (userAgent.indexOf("QQBrowser") != -1) {
				// QQ
				this.browserType = "QQBrowser";
			} else if (userAgent.indexOf("Firefox") != -1) {
				// Firefox
				this.browserType = "Firefox";
			} else if (userAgent.indexOf("Chrome") != -1 && userAgent.indexOf("Safari") !== -1) {
				// chorme
				this.browserType = "Chrome";
				isShowBrowserWarning = false;
			} else if (userAgent.indexOf("Safari") != -1 && userAgent.indexOf("Chrome") === -1) {
				// Safari
				this.browserType = "Safari";
			}
			if (isShowBrowserWarning) {
				this.showBrowserWarning();
			}
		},
		// 
		showBrowserWarning() {}
	}
};
</script>

<style lang="less" scoped>
.site {
	height: 100%;
	width: 100%;
}
</style>
