<template>
	<!--  -->
	<div class="table-column-enum">
		<el-tag @click="clickEvent" v-bind="tag">{{ label }}</el-tag>
		<el-dialog :title="isHasCheckbox ? '' : tipsText" :visible.sync="isShow" :append-to-body="true">
			<div class="careful-container">
				<template v-if="isHasCheckbox">
					<div class="message-contents">{{ tipsText }}</div>
					<div class="choose">
						<el-checkbox v-model="checked">!</el-checkbox>
					</div>
				</template>
				<el-radio-group v-model="inputValue" v-else>
					<el-radio :label="item.key" v-for="(item, index) in enumList" :key="index">{{ item.name }}</el-radio>
				</el-radio-group>
			</div>
			<div slot="footer" class="dialog-footer">
				<el-button @click.stop.prevent="isShow = false"> </el-button>
				<el-button type="primary" @click.stop.prevent="changeValue"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>
<script>
export default {
	data() {
		return {
			label: "",
			tag: {
				size: "small",
				effect: "dark"
			},
			isShow: false,
			checked: false, // 
			inputValue: null, // 
			enumList: [], // 
			isSubmiting: false
		};
	},
	props: {
		value: [Boolean, String, Number], // 
		click: Function, // ,Promise
		permissionValue: String, // 
		tips: String, // "{0}"
		isShowConfirmTips: {
			type: Boolean,
			default: true
		}, // 
		list: {
			type: [String, Array],
			require: true
		}, // enumStringconstantsenum, Array)
		columnLabel: String // label
	},
	computed: {
		isBooleanEnum() {
			// boolean
			return this.enumList.length == 2;
		},
		hasPermission() {
			if (!this.permissionValue) {
				return true;
			}
			return site.globalService.hasPermission(this.permissionValue);
		},
		tipsText() {
			if (!this.isBooleanEnum) {
				return this.tips ? this.tips : "" + this.columnLabel + "";
			}
			let i = this.enumList.findIndex(item => item.key == this.value);
			if (i == -1) {
				return "" + this.columnLabel + "";
			} else {
				i = i == 0 ? 1 : 0;
				return this.tips ? "" + site.utils.stringFormat(this.tips, this.enumList[i].name) + "?" : "" + this.columnLabel + "" + this.enumList[i].name + "?";
			}
		},
		isHasCheckbox() {
			if (!this.isBooleanEnum) {
				return false;
			}
			let i = this.enumList.findIndex(item => item.key == this.value);
			return i != -1;
		}
	},
	watch: {
		isBooleanEnum() {
			this.generateTag();
		},
		value() {
			this.generateTag();
		}
	},
	mounted() {
		this.init();
	},
	methods: {
		init() {
			let list;
			if (typeof this.list == "string") {
				list = site.constants.ENUM[this.list];
			} else {
				list = this.list;
			}
			if (!list || list.length == 0) {
				throw Error("!");
			}
			this.enumList = site.utils.extend(true, list);
			this.generateTag();
		},
		// 
		generateTag() {
			this.inputValue = this.value;
			this.tag.effect = this.isBooleanEnum ? "dark" : "light";
			let i = this.enumList.findIndex(item => item.key == this.value);
			if (i == -1) {
				this.label = "";
				this.tag.type = "info";
			} else {
				this.label = this.enumList[i].name;
				this.tag.type = this.isBooleanEnum && i == 0 ? "success" : "";
			}
		},
		// 
		clickEvent(e) {
			if (!this.click || !this.hasPermission) {
				return;
			}
			e.preventDefault();
			e.stopPropagation();
			if (this.isShowConfirmTips || !this.isHasCheckbox) {
				this.isShow = true;
			} else {
				this.changeValue();
			}
		},
		// 
		changeValue() {
			if (this.isSubmiting == true) {
				this.$message.error("");
				return;
			}
			let selectValue = null;
			if (this.isHasCheckbox) {
				// 
				let i = this.enumList.findIndex(item => item.key == this.value);
				if (i == 0) {
					i = 1;
				} else {
					i = 0;
				}
				selectValue = this.enumList[i].key;
			} else {
				selectValue = this.inputValue;
			}
			if (this.value == selectValue) {
				this.$message.error("");
				return;
			}
			this.isSubmiting = true;
			this.click(selectValue)
				.then(result => {
					this.$emit("input", selectValue);
					if (this.isShowConfirmTips) {
						this.$emit("change-show-confirm-tips", !this.checked);
					}
					this.isShow = false;
					this.isSubmiting = false;
				})
				.catch(error => {
					this.isSubmiting = false;
				});
		}
	}
};
</script>
<style lang="less" scoped>
.el-tag {
	cursor: pointer;
}
/**  **/
.careful-container {
	padding-left: 20px;

	.message-contents {
		color: #000;
	}

	.choose {
		margin-top: 25px;

		.el-checkbox__label {
			font-size: 12px;
			color: #48576a;
		}
	}
}
</style>
